  .filterBox {
    width: 20vmax;
    position: static;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .categoryBox{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }

  .page-link {
    font: 300 1.7vmax "Roboto";
  }
  .category-link {
